.stats-section {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 264px;
    height: 130px;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
    background-color: #fff;
    border-radius: 2px;
    font-family: 'Open Sans', sans-serif;
}

.title {
    margin: 0;
    text-align: center;
    font-size: 18px;
    font-weight: 600;
    line-height: 70px;
    text-transform: uppercase;
    color: #53565b;
}

.stat-list {
    display: flex;
    height: 60px;
    margin: 0;
    padding: 0;
    list-style: none;
}

.stat-list li {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: calc(100%/4);
}

.stat-list li:first-child{
    border-radius: 0px 0px 0px 2px;
}

.stat-list li:last-child{
    border-radius: 0px 0px 2px 0px;
}

.percentage{
    font-weight: 700;
    color: #fff;
}

.label {
    font-size: 12px;
    color: #fff;
}